@smartphones: ~"only screen and (max-width: 650px)";
@tablets: ~"only screen and (min-width: 768px) and (max-width: 959px)";



html, body, #contact {
	height: 100%;
}



.box {
  position: fixed;
  text-align: center;
  overflow: hidden;
  z-index: -1;
  opacity: 0;
  width: 100%;
  height: 100%;
  left: 0px;
  top: 0px;
  background: rgba(0, 0, 0, 0.8);
  -webkit-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
  ul {
    position: relative;
    top: 20%;
    // -webkit-transform: scale(2);
    // -moz-transform: scale(2);
    // -ms-transform: scale(2);
    // transform: scale(2);
    -webkit-transition: all 0.3s ease-in-out;
    -moz-transition: all 0.3s ease-in-out;
    -o-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
  }
  li {
    display: inline-block;
    margin: 20px;
  }
  li a {
    border-radius: 3px;
    padding: 15px;
    border: 1px solid transparent;
    text-decoration: none;
    font-size: 18px;
    color: #fff;
    -webkit-transition: all 0.2s ease-in-out;
    -moz-transition: all 0.2s ease-in-out;
    -o-transition: all 0.2s ease-in-out;
    transition: all 0.2s ease-in-out;
  }
  li a:hover {
    border-color: #fff;
  }
  li a i {
    margin-right: 5px;
    font-size: 24px;
  }
}

#toggle-nav-label {
  color: rgba(0, 0, 0, 0.5);
  background: rgba(0, 0, 0, 0.2);
  text-align: center;
  line-height: 30px;
  font-size: 16px;
  display: block;
  cursor: pointer;
  position: relative;
  z-index: 500;
  width: 30px;
  height: 30px;
  border-radius: 5px;
}

#toggle-nav {
  display: none;
}

#toggle-nav:checked ~ .box {
  opacity: 1;
  z-index: 400;
}

#toggle-nav:checked ~ .box ul {
  // -webkit-transform: scale(1);
  // -moz-transform: scale(1);
  // -ms-transform: scale(1);
  // transform: scale(1);
}

#toggle-nav:checked ~ #toggle-nav-label {
  background: #fff;
  color: rgba(0, 0, 0, 0.8);
}



#contact {
	height: 100%;
  background: url('../images/home-bg.png');
  background-color: #eff8f9;
  height: 100vh;
  padding: 25px 75px 25px 25px;
  display: flex;
  flex-direction: column;


  .navigation-main {
    display: flex;
    justify-content: space-between;
    align-content: flex-end;
    .logo {
      width: 110px;
      height: 27px;
      background: url('../images/logo.png') no-repeat 0 0;

      &:before {
        content: ' ';
        width: 100px;
        background: #e3f5f8;
        display: block;
        height: 6px;
      }
    }
    .navigation-bars, .languages-mobile {
    	display: none;
    }

  }

  ul.nav {
    display: flex;
    list-style: none;
    padding: 0;
    li {
      a {
        padding: 0 10px;
        color: #131313;
        font-family: 'Brandon Black';
        text-transform: uppercase;
        font-size: 14px;
      	opacity: .6;
      	text-decoration: none;

        &:hover {
        	opacity: 1;
        }
      }
    }
  }

  .content {
  	height: 100%;
  	display: flex;
  	// flex-direction: column;
  	padding-top: 10vh;
  	& > * {
  		// flex: 1
  	}

  	.main-content {
  		display: flex;
	  	flex-direction: column;
			align-items: stretch;
			flex: 1;
  		& > * {
  			// display: flex;
	  		// flex: 1
	  	}

	  	.navigation-horizontal {
	  		ul {
	  			display: flex;
    			list-style: none;
    			justify-content: flex-end;
					align-items: center;

	  			li {
	  				padding: 10px;
	  				// flex:.2;
	  				transform: rotate(270deg);
	  				a {
	  					text-decoration: none;
	  					// color: #b3cfd9;
	  					color: #1d9dbd;
	  					font-family: 'PFDinTextPro-Regular';

	  					&:hover, &.active {
	  						color: #1d9dbd;
	  					}
	  				}
	  			}

	  			li.leading-line {
	  				width: 140px; 
	  				transform: rotate(0deg);

	  				&:before {
	  					content: ' ';
	  					background: #cbdbe0;
							height: 1px;
							width: 100px;
							display: block;
	  				}
	  			}
	  		}
	  	}

  		.top {
  			display: flex;
			  flex-flow: column;
				height: 100%;
			  flex: 3;
			  // padding-top: 10%;

				.headline {
					// position: relative;
					// top: 0;
					// right: 0;
					color: #1d9dbd;
					font-size: 80.5px;
					text-transform: uppercase;
					font-family: 'Brandon Black';

					display: flex;
	  			flex-direction: row;
	  			flex-wrap: wrap;
  				justify-content: flex-end;
  				.first {
  					width: 100%;
  				}
  				.break {
  					// width: 100%;
  				}
  				.second {
  					width: 100%;
  				}
				}
  		}

  		.footer {
  			display: flex;
					align-items: center;

				.copyright {
					flex: 3;
					font-family: 'Proxima Nova';
					font-size: 14px;
					color: #9db7c1;

				}

  			.social {
					flex: 2;
  				display: flex;
					align-items: center;

					.box {
						margin: 8px;

						i {
							color: #ef7c00;
							font-size: 15px;
						}
					}

  			}
  			.cta {
  				padding-left: 40px;
					font-family: 'Brandon Bold';
					font-size: 14px;

					a {
						text-decoration: none;
						color: #1d9dbd;
					}
  			}
  		}
  	}
  }

  .sidenav {
		position: absolute;
		top: 0;
		right: 54px;
		width: 10px;
		word-break: break-all;
  }
}

@media @smartphones,
@tablets {
  // @import 'media';
  #contact {
    padding: 10px;

    .navigation-main {
			flex-direction: row;
			justify-content: space-between;
			align-items: center;


    	.left-side {

    	}
    	
    	.navigation-bars {
      	display: flex;

      	i {
      		 align-self: center;
      		 font-size: 24px;
      	}
      }

      ul.nav {
        display: none;
        flex-direction: column;

        &.open {
          display: flex;
          position: absolute;
          margin: 0 auto;
          background: rgb(255,255,255,.3);
          width: 100%;
				  left: 0;
				  text-align: center;
				  height: 100%;
				  justify-content: center;

        }
      }

      .languages-mobile {
      	display: block;

      	ul {
      		padding: 0;
      		margin: 0;
      		list-style: none;

      		li {
      			color: #1d9cbd;
      			background: #eff7f9;
      			border: 1px solid #cadbe2;
						border-radius: 50%;
						padding: 5px;
      		}
      	}
      }
      
    }
    .content {
      .main-content {
        .navigation-horizontal {
        	display: none;
        }
        .top {
        	justify-content: flex-end;
          .headline {
            font-size: 24px;

            .first, .second {
            	width: auto;
            }

            .aligner {
							display: flex;
							justify-content: space-around;
							width: 100%;
							flex-wrap: wrap;
            }
          }
        }
      }
      .footer {
    	  flex-direction: column;
				align-items: flex-start !important;
        
        .copyright {
        	order:1
        }
        
        .social {
        	order: 0;

        	.box {
        		i {
	        		color: #1d9dbd !important;
        		}

        		&:first-child {
        		margin-left: 0;
	        	}
        	}
        }
        
        .cta {}
      }
    }
  }
}

